<template>
	<div class="hand">
		<div class="header">
			<img :src="picUrl"> <br>
			<img src="../images/touying.png">
		</div>
		<div class="userName">
			<label for="userName">用户名</label>
			<input id="userName" type="text" placeholder="如：田小姐" v-model="userName">
		</div>
		<div class="jdName">
			<label for="jdName">账单名</label>
			<input type="text" placeholder="如：购买手机" v-model="jdName" id="monthJe">
		</div>
		<div class="title">还款信息</div>
		<div class="monthJe">
			<label for="monthJe">月供金额</label>
			<input type="number" placeholder="如：100" v-model="monthJe" id="monthJe">
		</div>
		<div class="hq" @click="explame">
			<mt-cell title="还款方式" is-link>
				<input type="text" v-model="selectHq" placeholder="请选择还款方式">
			</mt-cell>
		</div>
		<div class="TotalNumber">
			<label for="TotalNumber">总期数</label>
			<input type="number" placeholder="请输入总期数" id="TotalNumber" v-model="TotalNumber">
		</div>
		<div class="currentNumber">
			<label for="currentNumber">当前期数</label>
			<input type="number" placeholder="请输入当前期数" id="currentNumber" v-model="currentNumber">
		</div>
		<div class="title">还款提醒</div>
		<div class="daysHq" @click="explame1">
			<mt-cell title="还款日" is-link>
				<input type="text" v-model="daysHq" placeholder="请选择还款日">
			</mt-cell>
		</div>
		<div class="submitBtn" @click="save">提交</div>
		<vue-pickers :show="show1"
    		:selectData="pickData1"
    		v-on:cancel="close"
    		v-on:confirm="confirmFn">
    	</vue-pickers>
    	<vue-pickers :show="show2"
    		:selectData="pickData2"
    		v-on:cancel="close"
    		v-on:confirm="confirmFn2">
    	</vue-pickers>
	</div>
</template>
<script>
import { getPlatformInfo, saveEecords } from './../api/api'
import { getStore } from '../config/mUtils'
import { Toast } from 'mint-ui'
import VuePickers from 'vue-pickers'
export default {
	data() {
		return {
			id: null,
			pcode: null,
			picUrl: null,
			userName: null,
			jdName: null,
			monthJe: null,
			selectHq: null,
			TotalNumber: null,
			currentNumber: null,
			userId: null,
			daysHq: null,
			show1: false,
			show2: false,
			repayType: null,
			pickData1: {
				columns: 1,
				pData1: [
					{
						text: '一次性还款',
						value: 0
					},
					{
						text: '分期还款',
						value: 1
					}
				]
			},
			pickData2: {
				columns: 1,
				pData1: [
					{
						text: '每月1号',
						value: 12
					},
					{
						text: '每月2号',
						value: 13
					},
					{
						text: '每月3号',
						value: 14
					},
					{
						text: '每月4号',
						value: 15
					},
					{
						text: '每月5号',
						value: 16
					},
					{
						text: '每月6号',
						value: 17
					},
					{
						text: '每月7号',
						value: 18
					},
					{
						text: '每月8号',
						value: 19
					},
					{
						text: '每月9号',
						value: 20
					},
					{
						text: '每月10号',
						value: 21
					},
					{
						text: '每月11号',
						value: 22
					},
					{
						text: '每月12号',
						value: 23
					},
					{
						text: '每月13号',
						value: 24
					},
					{
						text: '每月14号',
						value: 25
					},
					{
						text: '每月15号',
						value: 26
					},
					{
						text: '每月16号',
						value: 27
					},
					{
						text: '每月17号',
						value: 28
					},
					{
						text: '每月18号',
						value: 29
					},
					{
						text: '每月19号',
						value: 30
					},
					{
						text: '每月20号',
						value: 31
					},
					{
						text: '每月21号',
						value: 32
					},
					{
						text: '每月22号',
						value: 33
					},
					{
						text: '每月23号',
						value: 34
					},
					{
						text: '每月24号',
						value: 35
					},
					{
						text: '每月25号',
						value: 36
					},
					{
						text: '每月26号',
						value: 37
					},
					{
						text: '每月27号',
						value: 38
					},
					{
						text: '每月28号',
						value: 39
					},
					{
						text: '每月29号',
						value: 40
					},
					{
						text: '每月30号',
						value: 41
					},
					{
						text: '每月31号',
						value: 42
					}
				]
			}
		}
	},
	beforeCreate() {
		document.getElementsByTagName('body')[0].className = 'active' 
	},
	beforeDestroy() {
		document.body.removeAttribute('class', 'active')
	},
	methods: {
		init() {
			this.id = getStore('id')
			this.pcode = getStore('pcode')
			this.userId = getStore('userId')
			let params = {
				id: this.id,
				pCode: this.pcode
			}
			getPlatformInfo(params).then(res => {
				if (res.code === 200) {
					this.id = res.data[0].id
					this.pcode = res.data[0].pcode
					this.picUrl = res.data[0].picUrl
				}
			})
		},
		save() {
			let params = {
				pCode: this.pcode,
				userId: this.userId,
				userName: this.userName,
				ordersName: this.jdName,
				amtByMonth: this.monthJe,
				repayType: this.repayType,
				totalPeriods: this.TotalNumber,
				currentPeriods: this.currentNumber,
				repayDay: this.daysHq,
				repayRemind: '',
				repayStatus: 0
			}
			saveEecords(params).then(res => {
				if (res.code === 200) {
					Toast({
						message: res.msg,
						position: 'middle',
						duration: 2000
					});
					this.$router.push({path: '/jizhang'})	
				} else {
					Toast({
						message: res.msg,
						position: 'middle',
						duration: 2000
					});
				}
			})

		},
		explame() {
			this.show1 = true
			this.show2 = false
		},
		explame1() {
			this.show2 = true
			this.show1 = false
		},
		close() {
			this.show1 = false
			this.show2 = false
		},
		confirmFn(data) {
			this.selectHq = data.select1.text
			this.repayType = data.select1.value
			this.close()
		},
		confirmFn2(data) {
			this.daysHq = data.select1.text
			this.close()
		}
	},
	mounted() {
		this.init()
	},
	components: {
  		VuePickers
	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.hand {
		background: #f2f3f2;
		height: 100%;
		.header {
			width: 100%;
			height: 2.01rem;
			background-image: linear-gradient(180deg, #d94a49 0%, #f36d47 100%);
			text-align: center;
			img {
				width: 1rem;
				height: 1rem;
				border-radius: 0.3rem;
				margin-top: 0.35rem;
			}
			img:nth-child(3) {
				width: 0.82rem;
				height: 0.09rem;
				margin-top: 0.1rem;
			}
		}
		.userName, .jdName, .monthJe, .TotalNumber, .currentNumber {
			height: 0.77rem;
			line-height: 0.77rem;
			color: #333;
			letter-spacing: 0.022rem;
			font-size: 0.28rem;
			background: #fff;
			padding: 0 0.3rem;
			input {
				outline: none;
				margin-left: 0.2rem;
				font-size: 0.24rem;
			}
			::-webkit-input-placeholder {
				color: #999;
			}
			::-moz-input-placeholder {
				color: #999;
			}
		}
		.daysHq, .hq {
			.mint-cell-wrapper {
				padding: 0 0.3rem;
				.mint-cell-title {
					flex: initial;
					margin-right: 0.2rem;
					.mint-cell-text {
						vertical-align: top;
					}
				}
				.mint-cell-value {
					flex: initial;
					display: inline-block;	
				}
				input {
					color: #333;
					font-size: 0.24rem;
				}
			}
		}
		.title {
			height: 0.6rem;
			line-height: 0.6rem;
			color: #999;
			padding-left: 0.3rem;
		}
		.submitBtn {
			width: 4.97rem;
			height: 1rem;
			line-height: 1rem;
			border-radius: 0.5rem;
			color: #fff;
			background-image: linear-gradient(0deg, #f16d4d 1%, #d64a4d 100%);
			margin: 0.6rem auto 0;
			text-align: center;
			font-size: 0.32rem;
		}
	}
	.active {
		overflow-y: hidden; 
	}
</style>



